
import { defineComponent } from "vue";
export default defineComponent({
  name: "LoginPage",
  data() {
    return {
      username: "",
    };
  },

  methods: {
    goToChat() {
      if (this.username && this.username.replaceAll(" ", "")) {
        this.$router.push({
          name: "Chat",
          query: { username: this.username },
        });
        return;
      }
      alert("Enter username");
    },
  },
});
